body {
    font-family: cursive;
    background-color: antiquewhite;
}

a {
    text-decoration: none;
    color: indigo;

}

ul {
    list-style-type: none;
}

h1 {
    position: relative;
    z-index: 2;
    color: darkred;
    margin-left: 5px;
}

.header {
    display: flex;
    justify-content: space-between;
}

.header-burger:hover,
.nav-item:hover {
    cursor: pointer;
    color: #114e09;
    transform: scale(1.1);
    font-weight: bold;
}

.header-burger {
    display: block;
    position: relative;
    height: 24px;
    width: 24px;
    top: 15px;
    // left: -20px;
    margin-left: 7%;
    z-index: 5;
}

.header-burger::before,
.header-burger::after {
    content: '';
    background-color: rgb(41, 4, 4);
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
}

.header-burger::before {
    top: 0;
}

.header-burger::after {
    bottom: 0;
}

.header-burger span {
    position: absolute;
    background-color: rgb(41, 4, 4);
    width: 100%;
    left: 0;
    height: 2px;
    top: 11px;
}

.burger-transform {
    transform: rotate(90deg);
    transition: 0.5s;
}

.header-nav {
    display: flex;
    position: absolute;
    font-size: x-large;
    top: 0;
    height: 100vh;
    width: 260px;
    // background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(230,45,253,1) 100%);
    background: linear-gradient(0deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
    left: -500px;
    transition: transform 1s;
    z-index: 3;
}

.move {
    transform: translateX(500px);
}

.header-nav li {
    margin-top: 10px;
}

//  switch

.switcher {
    margin-right: 12%;
}

.hidden {
    position: absolute;
    top: -9999px;
    left: -9999px;
    visibility: hidden;
}

.center {
    position: relative;
    top: 50%;
    left: 50%;
    /* bring your own prefixes */
    transform: translate(-50%, -50%);
}

#switch {
    background-color: mediumorchid;
    width: 130px;
    height: 60px;
    display: inline-block;
    border-radius: 200px;
    cursor: pointer;
    transition: background-color 0.5s;
}

#switch::before {
    content: "";
    display: inline-block;
    width: 52px;
    height: 52px;
    background-color: white;
    border-radius: 50%;
    position: relative;
    left: 3px;
    transition: left 0.5s;
    top: 4px;
}

#switch-checkbox:checked+label {
    background-color: darkseagreen;
}

#switch-checkbox:checked+label::before {
    left: 75px;
}

.switch-label {
    font-size: 30px;
    margin-left: -50px;
}

//  main page

.container {
    display: flex;
    margin: 0 auto;
    max-width: 1620px;
    flex-wrap: wrap;
    justify-content: center;
    // height: 60vh;
    align-items: center;
}

.container>div {
    display: flex;
    flex: 0 1 20%;
    margin: 12px;
    /* justify-content: center; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.category-name {
    position: absolute;
    font-size: 25px;
    font-weight: 900;
    top: 8%;
}

.category-item img {
    width: 360px;
    height: 265px;
    box-shadow: rgba(0, 0, 0, 0.603) 5px 5px 15px;
    border: 35px solid darkseagreen;
    box-sizing: border-box;
    border-radius: 50%;
}

.category-item p {
    position: relative;
    font-weight: bold;
    font-size: 25px;
    color: rgb(128, 10, 79);
    bottom: 20px;
}

.card-card button {
    position: relative;
    bottom: 30px;
}

.card-card:hover {
    transform: scale(1.05);

    & img {
        box-shadow: rgba(0, 0, 0, 0.603) 15px 15px 15px;
        transform: scale(1.05);
        transition: all 1s;
        cursor: pointer;
    }
}

.card-card p {
    position: relative;
    font-weight: bold;
    font-size: 25px;
    color: darkslategrey;
    bottom: 40px;
}

.start-btn {
    width: 150px;
    height: 50px;
    border-radius: 50%;
    background-color: rgb(44,185,198);
    outline: none;
    box-shadow: 5px 5px 10px grey;
}

.starBox {
    margin-left: 10px;
}

.pink {
    color: pink;
}

@media all and (max-width: 650px) {
    .container {
        margin-top: 70px;
    }
    .category-name {
        margin-top: 80px;
    }
    .category-item img {
        width: 280px;
        height: 230px;
    } 
    .card {
        margin: 0;
        height: 280px;
    }    
}